.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.random-word {
  font-size: 25px;
  font-weight: 500;
  margin-top: 10px;
}

input {
  all: unset;
  background-color: white;
  color: black;
  width: 200px;
  height: 30px;
  border-radius: 5px;
  margin-top: 60px;
}

button {
  all: unset;
  height: 30px;
  border-radius: 5px;
  margin: 10px;
}

.new-word {
  width: 200px;
}
.word-number {
  width: 50px;
}
